import sexBar from "../common/sex-bar.vue"
export default {
	props:{
		item: Object,
		curidx: Number
	},
	data(){
		return{
			dianzan: this.item.interaction.like,
			isshare: this.item.share,
			iscomment:  this.item.comment,
			isLike:  this.item.like,
			isFocus: this.item.focus
		}
	},
	methods:{
		focus() {
			this.isFocus = true
		},
		share() {
			console.log('转发')
		},
		comment() {
			console.log('评论')
		},
		like() { // 点赞
			console.log(this.isLike)
			if(!this.isLike){
				this.isLike = true
				this.dianzan++
				uni.showToast({
					title:'点赞成功'
				})
			}else{
				this.isLike = false
				this.dianzan--
				uni.showToast({
					title:'取消成功'
				})
			}
			
			
		}
	},
	components:{
		sexBar
	}
}
